import React, { Component } from 'react'
import './main.css'

export default class Card extends Component {
  render() {
    return (
      <div className="pro-card">
        <div>
          <img src="https://picsum.photos/300" alt="" />
        </div>
        <div>
          <p>
            <em>
              <b>product title</b>
            </em>
          </p>
        </div>
        <div>
          <button>View</button>
          <button>Add to cart</button>
        </div>
      </div>
    )
  }
}
