import React, { Component } from 'react'
import './Navbar.css'
import { Link } from 'react-router-dom'

export default class Navbar extends Component {
  state = {
    open: true,
    show: true,
  }
  handleClick = () => {
    this.setState({ open: !this.state.open, show: !this.state.show })
    if (this.state.show) {
      document.querySelector('.navlist').style.display = 'flex'
    } else if (!this.state.show) {
      document.querySelector('.navlist').style.display = 'none'
    }
    console.log('working', this.state.show)
  }
  render() {
    return (
      <div className="navbar">
        <ul className="navlist">
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/about">About</Link>
          </li>
          <li>
            <Link to="/products">Products</Link>
          </li>
          <li>
            <Link to="/contact">Contact</Link>
          </li>
        </ul>
        <i
          className={this.state.open ? 'fas fa-bars' : 'fas fa-times'}
          onClick={this.handleClick}
        ></i>
      </div>
    )
  }
}
